*{
  Margin: 0;
  Padding: 0;
  Box-sizing: border-box;
}
.App{
  display: flex;
  justify-content: space-between;
}
main{
  display: flex;
  flex-flow: row;
  align-items: start;
  justify-content: space-between;
  min-height: 100vh;
}
/* 섹션1,3 */
.form__container, .lastSection{
  width: 300px;
}
/* 섹션2 질문란 */
h1{
  font-size: 100px;
  color: rgb(136, 136, 136);
  padding-bottom: 40px;
  border-bottom: 3px solid rgb(226, 226, 226);
  position: sticky;
  top: 150px;
  text-align: left;
}
.form{
  margin-right: 50px;
  margin-top: 40px;
  text-align: right;
}

.discussion__wrapper{
  display: flex;
  justify-content: center;
}
.discussions__container{
  display: flex;
  flex-flow: column;
  gap: 10px;
  margin-top: 40px;
}
#name, #title, #story, #search{
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 2px;
  padding-right: 2px;
}
#story{
  height: 200px;
}
#submit{
  margin-left: 50px;
}
/* 하나의 디스커션 */
.discussion__container{
  display: flex;
  flex-flow: row;
  width: 600px;
  justify-content: space-between;
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid gray;
}
/* 프로필 사진 */
img{
  width: 48px;
  border-radius: 50%;
}
/* 가운데 디스커션 */
.discussion__content{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 15px;
  padding-bottom: 10px;
  gap: 15px;
}
.discussion__answered{
  display: flex;
  align-items: center;
  padding-left: 20px;
}
.discussion__title{
  text-align: left;
  font-size: 15px;
}
.discussion__title > a{
  text-decoration-line: none;
  color: black;
}
.discussion__information{
  text-align: right;
  font-size: 13px;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
